<template>
    <div id="content">
        <div class="page-header">
            <div class="container-fluid">
                <div class="pull-right">
                    <a href="#/design/banner/create" data-toggle="tooltip" title="" class="btn btn-primary"
                       data-original-title="添加"><i class="fa fa-plus"></i>
                    </a>
                    <a href="" data-toggle="tooltip" title=""
                       class="btn btn-default" data-original-title="重建"><i class="fa fa-refresh"></i>
                    </a>
                </div>
                <h1>横幅列表</h1>
                <ul class="breadcrumb">
                    <li><a href="">首页</a></li>
                    <li><a href="">横幅列表</a></li>
                </ul>
            </div>
        </div>
        <div class="container-fluid cye-lm-tag">
            <div class="panel panel-default cye-lm-tag">
                <div class="panel-heading">
                    <h3 class="panel-title"><i class="fa fa-list"></i> 横幅列表</h3>
                </div>
                <div class="panel-body cye-lm-tag">
                    <form action="http://opencart.cloud.com/admin/index.php?route=design/banner/delete&amp;user_token=91c2ab49f0ffc9edbbaaaa3810eb103c" method="post" enctype="multipart/form-data" id="form-banner">
                        <div class="table-responsive">
                            <table class="table table-bordered table-hover">
                                <thead>
                                <tr>
                                    <td style="width: 1px;" class="text-center"><input type="checkbox" onclick="$('input[name*=\'selected\']').trigger('click');"></td>
                                    <td class="text-left">                      <a href="http://opencart.cloud.com/admin/index.php?route=design/banner&amp;user_token=91c2ab49f0ffc9edbbaaaa3810eb103c&amp;sort=name&amp;order=DESC" class="asc">横幅名称</a>
                                    </td>
                                    <td class="text-left">                      <a href="http://opencart.cloud.com/admin/index.php?route=design/banner&amp;user_token=91c2ab49f0ffc9edbbaaaa3810eb103c&amp;sort=status&amp;order=DESC">状态</a>
                                    </td>
                                    <td class="text-right">管理</td>
                                </tr>
                                </thead>
                                <tbody>
                                <tr v-for="item in banner_list" >
                                    <td class="text-center">
                                        <input type="checkbox" v-model="selected_banner_id" v-bind:value="item.banner_id" >
                                    </td>
                                    <td class="text-left">{{item.name}}</td>
                                    <td class="text-left">
                                        <span v-show="item.status==1">启用</span>
                                        <span v-show="item.status==0">禁用</span>
                                    </td>
                                    <td class="text-right">
                                        <a v-bind:href="'#/design/banner/edit/' + item.banner_id "
                                           data-toggle="tooltip" title=""
                                           class="btn btn-primary" data-original-title="编辑">
                                            <i class="fa fa-pencil"></i>
                                        </a>

                                        <a  v-on:click="banner_remove( item )"
                                            class="btn btn-danger" data-original-title="删除">
                                            <i class="fa fa-trash-o"></i>
                                        </a>
                                    </td>
                                </tr>

                                </tbody>
                            </table>
                        </div>
                    </form>
                    <div class="row cye-lm-tag">
                        <div class="col-sm-6 text-left"></div>
                        <div class="col-sm-6 text-right cye-lm-tag">显示 1 - 4 / 合计 4（共 1 页）</div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "banner",

        data:function(){

            return {

                selected_banner_id:[],
                banner_list:[],

            }
        },

        methods:{

            get_list:function(  ){

                var t = this;
                var url = this.config.web_url + '/admin.php?r=banner';

                this.axios.get( url ).then(function( res ){

                    t.banner_list = res.data;

                });
            },

            banner_remove:function( banner ){

                var t = this;
                var url = this.config.web_url + '/admin.php?r=banner/delete';
                url += '&banner_id=' + banner.banner_id;

                this.axios.get( url ).then(function( res ){

                    t.get_list();

                });
            }
        },

        created:function(){

            this.get_list();

        }
    }
</script>

<style scoped>

</style>