<script>
    export default {
        name: "order",

        data:function(){

            return {

                order_list:[],

                selected_order:[]
            }
        },

        methods:{

            get_list:function(){

                var url = this.config.web_url + '/admin.php?r=order';
                var t = this;

                this.axios.get( url ).then(function( res ){

                    t.order_list = res.data;

                });
            }

        },

        created:function(){

            this.get_list();
        }

    }
</script>

